import { useState } from 'react';

const toggleDarkMOde = dark => {
    dark
        ? document.documentElement.classList.add('dark')
        : document.documentElement.classList.remove('dark');

}

const Moon = () => {
    const userDarkMode = window.matchMedia('(prefers-color-scheme: dark)').matches;
    console.log(`got user preference: ${userDarkMode}`);
    const [darkMode, setDarkMode] = useState(userDarkMode);
    toggleDarkMOde(darkMode);
    console.log(`run toogle once: ${darkMode}`)

    return (
        <svg onClick={() => setDarkMode(!darkMode)} xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 self-center mx-5 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
        </svg>
    );
};

export default Moon;
